@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap");

:root {
  --color-primary-hue:252;
  --dark-color-lightness: 17%;
  --light-color-lightness: 95%;
  --black-color-lightness: 10%;
  --white-color-lightness:100%;
   --color-name-lightness:0%;

  --color-dark: hsl("252", 30%, var(--dark-color-lightness));
  --color-light: hsl(252, 30%, var(--light-color-lightness));
  --color-gray: hsl(var(--color-primary-hue), 15%, 65%);
  --color-primary: hsl(var(--color-primary-hue), 75%, 60%);
  --color-secondary: hsl(252, 100%, 90%);
  --color-success: hsl(120, 95%, 65%);
  --color-danger: hsl(0, 95%, 65%);
  --color-white: hsl(252, 30%, var(--white-color-lightness));
  --color-black: hsl(252, 30%, var(--black-color-lightness));
  --color-name:hsl(252,30%,var(--color-name-lightness));

  --border-radius: 2rem;
  --card-border-radius: 1rem;
  --btn-padding: 0.6rem 2rem;
  --search-padding: 0.6rem 1rem;
  --card-padding: 1rem;

  --stick-top-left: 5.4rem;
  --stick-top-right: 0rem;

  --card-border-radius-1: 0.3rem;
  --card-border-radius-2: 0.5rem;
  --card-border-radius-3: 0.8rem;
  --card-border-radius-4: 2rem;
  --card-border-radius-5: 15rem;

  --color-primary-light: rgba(230, 229, 249, 0.221);
  --color-primary-variant: #5854c7;
  --color-red: #da0f3f;
  --color-red-light: rgba(185, 37, 72, 0.15);
  --color-green: #00c476;
  --color-gray-300: rgba(242, 242, 254, 0.3);
  --color-gray-200: rgba(242, 242, 254, 0.7);
  --color-bg: #0f0f3e;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
