.Profile-container{
  height: 80vh;
  display: grid;
  grid-template-columns: 18vw auto;
  position: relative;
  column-gap: 2rem;
}
.himfollowers{
  width:56vw;
  height: 200px;
  position: relative;
  display: flex;
  justify-content: center;
  transition: all 300ms ease;
  flex-direction: column;
}
.popunder{
  position: fixed;
  width:56vw;
  height: 150px;
  z-index: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  top:3.8rem;
  background-color: var(--color-light);
  transition: all 30ms ease;
}
.popunder {
  animation: fadeIn ease 600ms;
  -webkit-animation: fadeIn ease 600ms;
  -moz-animation: fadeIn ease 600ms;
  -o-animation: fadeIn ease 600ms;
  -ms-animation: fadeIn ease 600ms;
}
@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

.popunder i {
  top: 50%;
  transform: translateY(-50%);
  height: 40px;
  width: 40px;
  background: #fff;
  border-radius: 50%;
  font-size: 2rem;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.23);
  line-height: 20px;
  text-align: center;
  cursor: pointer;
  position: absolute;
  transition: all 300ms ease;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popunder i:first-child {
  transition: all 300ms ease;
  left: -20px;
}
.popunder i:last-child {
  transition: all 300ms ease;
  right: -20px;
}
.popunder .carousel {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: calc((100% / 3) - 12px);
  gap: 2rem;
  overflow: hidden;
   transition: all 300ms ease;

}
.carousel.dragging .request{
    cursor: grab;
    user-select: none;
}


.himfollowers i {
